<template>
  <div class="option">
    <div class="option-content">
      <div class="title">
        <img src="../../assets/img/optionOnceTit1.png" class="title-img" />
      </div>
      <div class="list-box">
        <div
          v-for="(item, key) in listOne.data"
          :key="key"
          class="list-item"
          :class="{style0: item.option === 0, style1: item.option === 1, style2: item.option === 2}"
          @click="itemClick(key, item.option, listOne)"
        >
          <div class="control-center">
            <div class="item-1 item">2020.01.30</div>
            <div class="item-2 item">15:00-16:00</div>
            <div class="item-3 item" v-text="item.option === 2?`(已选满)`:`(限${item.man}人)`"></div>
          </div>
        </div>
      </div>
    </div>
    <div class="option-content">
      <div class="title">
        <img src="../../assets/img/optionOnceTit2.png" class="title-img" />
      </div>
      <div class="list-box">
        <div
          v-for="(item, key) in list.data"
          :key="key"
          class="list-item"
          :class="{style0: item.option === 0, style1: item.option === 1, style2: item.option === 2}"
          @click="itemClick(key, item.option, list)"
        >
          <div class="control-center">
            <div class="item-1 item">2020.01.30</div>
            <div class="item-2 item">15:00-16:00</div>
            <div class="item-3 item" v-text="item.option === 2?`(已选满)`:`(限${item.man}人)`"></div>
          </div>
        </div>
      </div>
    </div>
    <div class="btn-group">
      <div class="confirm" @click="confirm">立即提交</div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 0 表示未选 1 表示选了 2 表示已选
      list: {
        name: '财富',
        data: [
          { date: '2020.01.30', time: '15:00-16:00', man: 40, option: 2 },
          { date: '2020.01.30', time: '15:00-16:00', man: 40, option: 0 },
          { date: '2020.01.30', time: '15:00-16:00', man: 40, option: 0 },
          { date: '2020.01.30', time: '15:00-16:00', man: 40, option: 0 }
        ]
      },
      listOne: {
        name: '南平',
        data: [
          { date: '2020.01.30', time: '15:00-16:00', man: 50, option: 2 },
          { date: '2020.01.30', time: '15:00-16:00', man: 40, option: 0 },
          { date: '2020.01.30', time: '15:00-16:00', man: 60, option: 0 },
          { date: '2020.01.30', time: '15:00-16:00', man: 20, option: 0 }
        ]
      },
      dataTit: '南平育享家',
      option: {name: '', key: ''}
    }
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    listData: {
      type: [Array, String],
      default () {
        return []
      }
    },
    // 默认单选
    radio: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    confirm () {
      const { name, key } = this.option
      if (key) {
        this.$message({
          type: 'success',
          message: `name: ${name}, key: ${key}, 选择成功`
        })
        this.$router.push({ path: '/ds/competition' })
      } else {
        this.$message({
          type: 'warning',
          message: `请先选择`
        })
      }
    },
    clickEv () {
      this.$emit('transmit', 'data')
    },
    itemClick (key, option, obj) {
      var arr = obj.data
      if (option === 2) return
      if (arr === this.list.data) {
        this.listOne.data.map(item => {
          if (item.option === 1) {
            item.option = 0
          }
        })
      } else {
        this.list.data.map(item => {
          if (item.option === 1) {
            item.option = 0
          }
        })
      }
      if (this.radio) {
        if (option === 1) return
        arr.map((item, index) => {
          if (item.option === 1) {
            item.option = 0
          }
        })
        arr[key].option = 1
        this.option.name = obj.name
        this.option.key = key
      } else {
        if (option === 1) {
          arr[key].option = 0
          return
        }
        arr[key].option = 1
        this.option.name = obj.name
        this.option.key = key
      }
    }
  }
}
</script>
<style lang="less">
.option {
  // 外层padding
  padding: 15px 11px;
  width: 100%;
  box-sizing: border-box;
  background: #f4f4f4;
  height: 100%;
  .option-content {
    width: 100%;
    padding: 16px 15px;
    background: #ffffff;
    border-radius: 6px;
    box-sizing: border-box;
    margin-bottom: 10px;
  }
  .title {
    font-size: 14px;
    color: #f5480a;
    letter-spacing: 1px;
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    .title-img {
      // width: 19vw;
      // height: 1.5vh;
      transform: scale(0.8);
    }
  }
  .list-box {
    width: 100%;
    .list-item {
      padding: 10px 0;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #333333;
      margin-bottom: 5px;
      border-radius: 4px;
      border: 1px solid transparent;
      .control-center {
        display: flex;
        align-items: center;
        justify-content: center;
        .item {
          margin-right: 8px;
          font-size: 12px;
          // color: #333333;
        }
      }
    }
    .style0 {
      background: #ffffff;
      border: 1px solid #dfdcdc;
    }
    .style1 {
      background: #f78924;
      color: #ffffff;
    }
    .style2 {
      background: #dfdcdc;
      color: #999999;
    }
  }
  .btn-group {
    width: 100%;
    margin-bottom: 80px;
    .confirm {
      margin-top: 20px;
      color: #ffffff;
      font-size: 16px;
      width: 95%;
      padding: 14px 0;
      letter-spacing: 1px;
      font-family: PingFang-SC-Bold;
      text-align: center;
      border-radius: 20px;
      background: #ff5701;
    }
  }
}
</style>



